.wrapper--about {
    background: url(../img/about/bg.png) no-repeat center/cover;
    position: relative;
    &::before {
        @include pseudo;
        width: 100%;
        height: 570px;
        background: url(../img/features/118.png) no-repeat center / 100% 100%;
        top: -150px; } }
.about {
    display: flex;
    &__photo {
        position: relative;
        margin-left: -200px;
        margin-top: 20px;
        left: 40px;
        width: 57vw;
        &::after {
            @include pseudo;
            width: 250px;
            height: 250px;
            border-radius: 50%;
            background: rgba($orange, .3);
            box-shadow: 0 0 950px rgba($orange, 1);
            left: 250px;
            bottom: 300px;
            z-index: -1; } }
    &__content {
        padding-top: 100px;
        h4 {
            text-transform: uppercase;
            margin-bottom: 15px; }
        p {
            @include font (16px, 900);
            color: rgba($white, .5);
            margin-bottom: 40px; } }
    &__social {
        display: flex;
        padding-top: 10px;
        &-item {
            display: flex;
            align-items: flex-start;
            width: 240px;
            height: 89px;
            background: url(../img/about/bg-item.png) no-repeat left top /105% 130%;
            padding-top: 16px;
            padding-left: 53px;
            margin-left: -35px;
            p {
                @include font (16px, 300);
                color: $white;
                b {
                    @include font (25px, 900); } }
            img {
                margin-top: 5px;
                margin-right: 10px; }
            &:nth-child(2) {
                img {
                    margin-right: 20px; } }
            &:nth-child(1) {
                margin-left: 0; } } } }

@media screen and (max-width: 1100px) {
    .about {
        flex-direction: column; }
    .about__content {
        padding-top: 0; } }

@media screen and (max-width: 700px) {
    .about__social {
        flex-wrap: wrap;
        justify-content: center; }
    .about__social-item {
        margin-left: 0;
        margin-bottom: 15px; }
    .about__photo {
        width: 100%;
        margin-left: -20%;
        img {
            max-width: 120%; }
        &::after {
            width: 35%;
            height: 35%;
            left: 30%;
            bottom: 29%; } } }
