.wraper--features {
    padding-top: 80px;
    &::before {
        @include pseudo;
        width: 100%;
        height: 570px;
        background: url(../img/features/118.png) no-repeat center / 100% 100%;
        top: -150px; } }
.features {
    &__right {
        position: absolute;
        right: 0;
        bottom: 50px;
        p {
            font-weight: 300;
            display: inline-block;
            position: absolute;
            top: -175px;
            left: 300px;
            &::before {
                @include pseudo;
                width: 32px;
                height: 81px;
                background: url(../img/features/dots.svg) no-repeat center;
                top: 60px;
                right: 20px; } } }
    &__block {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        max-width: 819px;
        padding-top: 70px; }
    &__item {
        width: 45%;
        margin-bottom: 75px;
        &-img {
            position: relative;
            height: 145px;
            width: 145px;
            border-radius: 50%;
            border: 1px solid $orange;
            margin-bottom: 40px;
            &::after {
                @include pseudo;
                width: 88px;
                height: 88px;
                border-radius: 50%;
                background: $orange;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                margin: auto; }
            &::before {
                @include pseudo;
                width: 310px;
                height: 310px;
                background: url(../img/features/shadow.png) no-repeat center/cover;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                border-radius: 50%; }
            img {
                position: absolute;
                right: -20px;
                bottom: 0;
                z-index: 2; } }
        p {
            font-weight: 30; } } }

@media screen and (max-width: 1490px) {
    .features__right {
        width: 40%;
        img {
            width: 100%; }
        p {
            left: 46%; } } }

@media screen and (max-width: 1120px) {
    .features__right {
        display: none; }
    .features__block {
        max-width: 100%; } }
@media screen and (max-width: 780px) {
    .features__item p {
        br {
            display: none; } } }
@media screen and (max-width: 480px) {
    .features__item {
        width: 100%;
        margin-bottom: 40px; }
    .features__item-img {
        margin-bottom: 15px; } }
