.wrapper--header {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2; }
.ycont--header {
    flex-direction: row;
    align-items: center; }
.header {
    &__logo {
        display: flex;
        align-items: center;
        margin-right: 41px;
        img {
            display: block;
            margin-right: 12px; }
        p {
            flex-grow: 1;
            @include font(11px, bold);
            max-width: 120px;
            line-height: 1.1;
            span {
                margin-top: 4px;
                display: inline-block;
                @include font (10px, normal); } } }
    &__descript {
        @include font (25px, 300);
        border-left: 1px solid $white;
        padding-left: 45px;
        padding-bottom: 5px; } }
