.wrapper--first {
    background: url(../img/bg.png) no-repeat left top /100% 110%;
    padding-top: 220px; }
.ycont--first {
    padding-bottom: 120px; }
.first {
    &__row {
        display: flex;
        justify-content: space-between; }
    &__left {
        display: flex;
        flex-direction: column;
        h1 {
            margin-bottom: 40px; } }
    &__descript {
        @include font (33px, 300);
        padding-left: 5px; }
    &__author {
        display: inline-block;
        margin-left: auto;
        margin-top: 72px;
        padding-right: 70px;
        b {
            @include font (19px, 900);
            margin-bottom: 12px;
            display: inline-block; }
        p {
            @include font (19px, 300); } }
    &__photo {
        position: absolute;
        bottom: 0;
        right: 395px;
        img:nth-child(2) {
            position: absolute;
            bottom: 165px;
            left: -20px; } } }


@media screen and (max-width: 1170px) {
    .first {
        &__row {
            flex-direction: column;
            // h1
            //     br
 }            //         display: none
        &__photo {
            left: 384px;
            right: unset;
            img:nth-child(2) {
                left: 90%;
                top: 62px; } } } }
@media screen and (max-width: 940px) {
    .wrapper--first {
        padding-top: 110px; }
    .header__descript {
        font-size: 18px; }
    .first__photo img:nth-child(2) {
        display: none; }
    .first__author {
        padding-right: 0; } }
@media screen and (max-width: 700px) {
    .first__photo {
        position: relative;
        left: 0;
        order: 1;
        margin-top: -110px; }
    .first__right {
        order: 2; }
    .wrapper--first {
        background: url(../img/bg.png) left top/100% no-repeat;
        padding-top: 130px;
        h1 {
            margin-bottom: 30px; } }
    .ycont--header {
        flex-wrap: wrap;
        div {
            margin-bottom: 20px; } }
    .first__descript {
        font-size: 27px;
        padding-left: 0; }
    .header__descript {
        padding-left: 20px; }
    .first__author {
        b {
            font-size: 17px;
            margin-bottom: 9px; }
        p {
            font-size: 17px; } }
    .ycont--first {
        padding-bottom: 0; } }
@media screen and (max-width: 510px) {
    .first__photo {
        margin-top: 20px; }
    .first__author {
        margin-top: 50px; }
    .form__block {
        padding: 30px 15px; }
    .header__descript {
        font-size: 11px;
        padding-left: 14px; }
    .header__logo p {
        font-size: 7px;
        max-width: 70px; }
    .header__logo p span {
        font-size: 6px; }
    .header__logo {
        margin-right: 15px; }
    .ycont--header div {
        margin-bottom: 0; }
    .header__logo img {
        width: 30px; }
    .wrapper--first {
        background: url(../img/bg-mob.png) no-repeat top left / 100%;
        h1 {
            margin-bottom: 14px; } }
    .first__descript {
        font-size: 18px; }
    .first__author {
        margin-left: 0; }
    .first__author b {
        font-size: 16px; }
    .first__author p {
        font-size: 14px; }
    .first__photo {
        position: absolute;
        width: 151px;
        right: 0;
        left: unset;
        top: 0;
        img {
            width: 100%; } }
    .first__left {
        margin-bottom: 120px; }
    .first__photo img:nth-child(2) {
        display: block;
        left: -15px;
        width: 50px;
        top: 222px; } }
@media screen and (max-width: 470px) {
    .form__img img:nth-of-type(2) {
        display: none; }
    .form__img img:nth-of-type(1) {
        top: -45px; } }
