.wrapper--portfolio{
    h2{
        margin-bottom: 55px;
    }
    background: url(../img/features/118.png) no-repeat center bottom / 100% 70%;
}
   
        

.grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 210px 210px;
    grid-template-areas:
        "item1 item1 item2 item3"
        "item1 item1 item4 item5";
    position: relative;
    .photo-item.item-1 {
        grid-area: item1;
    }
        
    .photo-item.item-2 {
        grid-area: item2;
    }
        
    .photo-item.item-3 {
        grid-area: item3;
    }
        
    .photo-item.item-4 {
        grid-area: item4;
    }
    .photo-item{
        position: relative;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%; 
            object-fit: cover;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            transition: all ease-in-out .45s;
        }
        &::before{
            @include pseudo;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: rgba(30,30,30,.4) url(../img/uncover.svg) no-repeat center;
            z-index: 2; 
            transition: all ease-in-out .25s;
            opacity: 0;
        }
        &:hover{
            img{
                transform: translate(-50%,-50%) scale(1.1);
            } 
            &::before{
                opacity: 1;
            }
        }
    }

}

@media screen and (max-width: 820px){
    .grid{
        grid-template-rows: 140px 140px;
    }
}
@media screen and (max-width: 640px){
    .grid{
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 150px 150px 180px 180px;
        grid-template-areas:
        "item1 item1"
        "item1 item1"
        "item2 item3"
        "item4 item5";
    }
}

@media screen and (max-width: 490px){
    .grid{
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 120px 120px 140px 140px;
        grid-template-areas:
        "item1 item1"
        "item1 item1"
        "item2 item3"
        "item4 item5";
    }
}

@media screen and (max-width: 380px){
    .grid{
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 80px 80px 120px 120px;
        grid-template-areas:
        "item1 item1"
        "item1 item1"
        "item2 item3"
        "item4 item5";
    }
}