.wrapper--program {
    background: url(../img/program/shutterstock.png) no-repeat left top/100% 110%; }
.program {
    &__block {
        background: $white;
        border-radius: 27px;
        box-shadow: 0 43px 43px rgba(#000, .44);
        color: $black;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 65px 65px 65px 84px;
        max-width: 795px;
        margin-left: auto;
        margin-top: 65px; }
    &__item {
        width: 45%;
        position: relative;
        padding-top: 30px;
        margin-bottom: 40px;
        img {
            position: absolute;
            right: 0;
            top: 0; }
        b {
            font-size: 50px; }
        p {
            font-weight: 300; }
        &:nth-child(1) {
            img {
                top: -30px;
                right: 20px; } }
        &:nth-child(2) {
            img {
                top: -50px;
                right: 15px; } }
        &:nth-child(3) {
            img {
                top: -30px;
                right: 20px; } }
        &:nth-child(4) {
            img {
                right: 25px; } }
        &:nth-child(5) {
            img {
                top: -40px; } }
        &:nth-child(6) {
            img {
                right: 30px;
                top: -10px; } } }
    &__right-img {
        position: absolute;
        right: 0;
        top: 80px; } }

@media screen and (max-width: 1320px) {
    .program__block {
        margin-right: 40px; } }
@media screen and (max-width: 1240px) {
    .program__block {
        margin-right: 80px; } }

@media screen and (max-width: 890px) {
    .program__right-img {
        display: none; }
    .program__block {
        margin-right: 0;
        width: 100%; } }

@media screen and (max-width: 810px) {
    .program__item p {
        br {
            display: none; } }
    .program__item:nth-child(4) img {
        right: 0; } }

@media screen and (max-width: 700px) {
    .program__item {
        width: 100%;
        margin-bottom: 60px; } }

@media screen and (max-width: 530px) {
    .program__block {
        padding: 50px 20px 0; }
    .program__item:nth-child(6) img {
        right: 0; } }
