html {
    font-size: 16px;
    overflow-x: hidden; }

body {
    font-family: $font;
    font-size: 20px;
    color: $white;
    background: $bg;
    margin: 0;
    overflow-x: hidden;
    width: 100%;
    min-width: 320px;
    -webkit-text-size-adjust: none; }

* {
    margin: 0;
    padding: 0; }

ul, li {
    display: block;
    padding: 0px;
    margin: 0px; }
li {
    position: relative;
    padding-left: 35px;
    margin-bottom: 40px;
    font-weight: 300;
    &::after {
        @include pseudo;
        width: 8px;
        height: 8px;
        background: $orange;
        left: 4px;
        top: 6px;
        border-radius: 50%; }
    &::before {
        @include pseudo;
        width: 16px;
        height: 16px;
        border: 1px solid $orange;
        border-radius: 50%;
        left: 0;
        top: 2px; } }

ol li {
    display: list-item; }

a {
    text-decoration: none; }

a:hover, a:focus {
    outline: none;
    text-decoration: none; }

h1 {
    @include font (82px, 300);
    font-family: $font;
    text-transform: uppercase;
    line-height: .7;
    margin: 50px 0;
    span {
        display: inline-block;
        @include font (41px, 900);
        &:last-child {
            margin-top: 30px;
            font-size: 53px; } } }
h2 {
    @include font (65px, 900);
    text-transform: uppercase; }
h3 {
    @include font (45px, 900); }
h4 {
    @include font (30px, 900); }




button {
    cursor: pointer;
    outline: none; }

.container {
    position: relative; }

.cover-bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
    video {
        width: 100%;
        height: 100%;
        object-fit: cover; }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }

.row {
    display: flex;
    flex-wrap: wrap; }


input, textarea {
    box-shadow: none;
    outline: none;
    width: 100%;
    max-width: 309px;
    border-radius: 27px;
    border: 1px solid $gray;
    padding: 0 56px;
    font-size: 16px;
    color: $black;
    margin-bottom: 25px;
    &::placeholder {
        // color: $gray
        font-weight: 300;
 } }        // opacity: 1
input {
    height: 55px;
    &[name=name] {
        background: url(../img/icon-name.png) no-repeat 25px center; }
    &[name=phone] {
        background: url(../img/icon-phone.png) no-repeat 25px center; }
    &[name=email] {
        background: url(../img/icon-mail.png) no-repeat 25px center; }
    &:hover {
        border-color: $orange; }
    &:focus {
        border-color: $black; } }
.form {
    background: $white;
    border-radius: 10px;
    padding: 15px;
    box-sizing: border-box;
    max-width: 445px;
    color: $black;
    &__block {
        border: 2px solid $border;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        position: relative;
        padding: 30px 35px; }
    &__img {
        position: absolute;
        right: 0;
        top: -40px;
        img {
            position: absolute;
            &:nth-of-type(1) {
                top: 0;
                right: 8px; }
            &:nth-of-type(2) {
                top: 117px;
                right: 85px; } } }
    &__title {
        @include font (16px, 900);
        color: $red;
        text-transform: uppercase;
        line-height: 1.4;
        margin-bottom: 5px; }
    &__descript {
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 28px; }
    .btn {
        margin: 10px 0 20px; } }

.btn {
    height: 55px;
    min-width: 208px;
    border-radius: 27.5px;
    font-size: 16px;
    color: $white;
    border: none;
    background: $gradient;
    &:hover {
        box-shadow: 0 5px 10px rgba(#f91a1a, .7); }
    &:active {
        box-shadow: 0 2px 10px rgba(#f91a1a, .7); } }

@media screen and (max-width: 970px) {
    h2 {
        font-size: 55px; } }
@media screen and (max-width: 700px) {
    h1 {
        font-size: 57px;
        span {
            font-size: 27px;
            &:last-child {
                font-size: 37px;
                margin-top: 20px; } } }
    .wrapper {
        padding: 25px 0; }
    h2 {
        font-size: 45px;
        br {
            display: none; } }
    body {
        font-size: 19px; }
    h4 {
        font-size: 27px; }
    h3 {
        font-size: 36px; } }


@media screen and (max-width: 510px) {
    h1 {
        font-size: 30px;
        margin: 20px 0;
        span {
            font-size: 16px;
            &:last-child {
                font-size: 20px;
                margin-top: 5px; } } } }

