.ycont--footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 30px;
    a {
        @include font (16px, 300);
        text-transform: uppercase;
        color: $white;
        margin-bottom: 7px;
        &:hover {
            text-decoration: underline; } } }
.footer {
    &__logo {
        margin-bottom: 7px; }
    &__policy {
        display: flex;
        flex-direction: column;
        a {
            text-decoration: underline;
            &:hover {
                text-decoration: none; } } }
    &__contacts {
        display: flex;
        flex-direction: column; } }

@media screen and (max-width: 700px) {
    .ycont--footer {
        flex-direction: column;
        padding: 0 15px;
        align-items: flex-start;
        &>div {
            margin-bottom: 15px;
            &:last-child {
                margin-bottom: 0; } } } }
