.wrapper--bonus {
    background: url(../img/bonus/bg.png) no-repeat center / cover;
    border-bottom: 1px solid rgba($white, .2);
    position: relative;
    padding-bottom: 0; }
.bonus {
    &__left {
        max-width: 660px;
        p {
            @include font (45px, 100);
            text-transform: uppercase;
            margin-bottom: 20px;
            margin-top: 10px; }
        h3 {
            text-transform: uppercase; }
        &-img {
            position: relative;
            img {
                position: absolute;
                &:nth-child(1) {
                    position: absolute;
                    right: 40%;
                    top: 30px; }
                &:nth-child(2) {
                    margin-left: -150px;
                    margin-top: -50px; } } } }
    &__photo {
        position: absolute;
        bottom: -11px;
        right: 363px; }
    &__right {
        padding-top: 150px;
        margin-bottom: 150px; } }

@media screen and (max-width: 1170px) {
    .bonus__left-img img:nth-child(1) {
        display: none; }
    .bonus__left-img img:nth-child(2) {
        right: 0;
        left: unset;
        width: 50%; }
    .bonus__left {
        max-width: 100%; }
    .bonus__photo {
        right: unset;
        left: 363px; } }

@media screen and (max-width: 890px) {
    .bonus__left-img img:nth-child(2) {
        display: none; }
    .bonus__right {
        padding-top: 50px;
        margin-bottom: 50px; }
    .bonus__photo {
        left: unset;
        right: 0;
        bottom: -10px;
        width: 40%;
        img {
            max-width: 100%; } }
    .wrapper--bonus {
        overflow: hidden; } }
@media screen and (max-width: 820px) {
    .bonus__photo {
 } }        // bottom: -9px
@media screen and (max-width: 700px) {
    .bonus__left p {
        font-size: 27px;
        br {
            display: none; } }
    .bonus__right {
        padding-top: 80px;
        margin-bottom: 100px; } }
